@import "../../assets/scss/app";

.rotateByScroll {
  .inner {
    position: relative;
    padding: 61px 16px 16px;

    @include desktop {
      padding: 89px 24px 24px;
      box-sizing: border-box;
      margin: 0 auto;
      width: 768px;
    }

    .texts {
      position: relative;
      z-index: 10;
    }

    .boxWrapper1 {
      position: absolute;
      z-index: 1;
      top: 61px;
      left: 16px;
      width: 200px;
      height: 200px;

      @include desktop {
        top: 89px;
        left: 24px;
      }
    }

    .boxWrapper2 {
      position: absolute;
      z-index: 11;
      top: 61px;
      right: 16px;
      width: 200px;
      height: 200px;

      @include desktop {
        top: 89px;
        right: 24px;
      }
    }

    .boxWrapper3 {
      position: absolute;
      z-index: 11;
      top: 300px;
      left: 0;
      width: 200px;
      height: 200px;
      transform: translateX(-100%);
    }

    .boxWrapper4 {
      position: fixed;
      z-index: 11;
      top: 50vh;
      right: 0;
      width: 200px;
      height: 200px;
      transform: translate(0, -50%);
    }

  }
}



