@import "../../assets/scss/app";

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 45px;
  z-index: 100;
  background: cadetblue;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  box-sizing: border-box;

  @include desktop {
    height: 65px;
    padding: 24px;
  }

  .left {
    display: flex;
    align-items: center;

    .logoWrapper {
      display: flex;
      align-items: center;

      .threejs, svg {
        width: 40px;
        height: 40px;

        @include desktop {
          width: 60px;
          height: 60px;
        }
      }

      svg g, .fiber {
        transition: 0.3s;
      }

      .fiber {
        background: #000;
        border-radius: 50%;

        margin-left: 10px;
        width: 30px;
        height: 30px;

        @include desktop {
          width: 45px;
          height: 45px;
        }
      }

      &:hover {
        svg g {
          stroke: #FFF;
        }

        .fiber {
          background: #FFF;
        }
      }
    }

    .label {
      margin-left: 20px;
      color: #FFF;
      font-size: 18px;
    }

  }




  .burgerMenu {
    @include onlyMobile(flex)
  }

}