@import "../../assets/scss/app";

.burgerMenu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: burlywood;
  z-index: 90;
  transform: translateX(101%);
  transition: 0.3s;
  padding-top: 45px;
  box-sizing: border-box;

  @include desktop {
    padding-top: 65px;
  }

  &_show {
    transform: translateX(0%);
  }

  .links {
    display: flex;
    flex-direction: column;
    padding: 16px;

    @include desktop {
      padding: 24px;
    }

    .link {
      text-decoration: none;
      color: #000;

      &:hover {
        text-decoration: underline;
      }
    }

  }
}