@import "../../assets/scss/app";

.home {
  padding-top: 45px;

  @include desktop {
    padding-top: 65px;
  }

  .links {
    display: flex;
    flex-direction: column;
    padding: 16px;

    @include desktop {
      padding: 24px;
    }

    .link {
      text-decoration: none;
      color: #000;

      &:hover {
        text-decoration: underline;
      }
    }

  }
}